import {request} from './request'

//获得费用月度表
export async function GetAllBudgetsPerMonths(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetAllBudgetsPerMonths',
        data:data
    })
}
//添加费用月度表
export async function InsertBudgetsPerMonth(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/InsertBudgetsPerMonth',
        data:data
    })
}
//删除费用月度表
export async function DeleteBudgetsPerMonth(data){
     return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteBudgetsPerMonth',
/*         url:'​/api/BusinessUnit/DeleteBudgetsPerMonth', */
        data:data
    })
}
