<template>
  <div id="summary" :class="{ display: isSameMonth }">
    <!-- 头部操作区 -->
    <el-row type="flex" justify="end" >
      <el-col :span="5"
        >
        <el-col :span="6">日期：</el-col>
        <el-col :span="17"><el-date-picker
          v-model="selectMonth"
          type="month"
          placeholder="选择日期"
          @change="changeDate"
        >
        </el-date-picker
      ></el-col>
      </el-col>
      <el-button type="info">导出Excel</el-button>
      <el-button type="info">预览</el-button>
      <el-button @click="add" type="info">添加</el-button>
      <el-button @click="removeUser" type="danger">删除</el-button>
    </el-row>
    <!-- 表单区 -->
    <el-row>
      <!--     <el-col class="table-header">
      <el-col style="line-height:40px;border-right:1px solid">费用月度预算表</el-col>
      <el-col class="text-left" style="border-top:1px solid;
      border-right:1px solid" :span="8" v-for="item in itemlist" :key="item.index">
        <el-col :span="10">{{ item.label }}</el-col>
        <el-col :span="14"><el-input :disabled="!isSameMonth" v-model="item.value"></el-input></el-col>
      </el-col>
    </el-col> -->
      <el-table
        border
        id="middle"
        show-summary
        :data="monthBugetTable"
        @select="rowClick"
      >
        <el-table-column label="费用月度预算表">
          <el-table-column type="selection" width="50px"></el-table-column>
          <el-table-column
            type="index"
            label="序号"
            fixed
            width="55px"
          ></el-table-column>
          <el-table-column
            v-for="item in monthBugetTableList"
            :key="item.index"
            :label="item.label"
            :width="item.width"
            :prop="item.prop"
          ></el-table-column>
          <!--         <el-table-column type="selection" width="50px"></el-table-column>
        <el-table-column
          type="index"
          label="序号"
          fixed
          width="55px"
        ></el-table-column>
        <el-table-column
          v-for="item in monthBugetTableList"
          :label="item.label"
          :width="item.width"
          :key="item.index"
        >
        <template slot-scope="scope">
              <el-input :disabled="!isSameMonth" v-model="scope.row[item.prop]"></el-input>
            </template>
        </el-table-column> -->
        </el-table-column>
      </el-table>
    </el-row>
    <!-- 新建弹框区 -->
    <el-dialog
      title="新建费用月度预算表"
      width="80%"
      :visible.sync="isNewDialogDisplay"
    >
    <el-row type="flex" justify="end">
      <el-button @click="addItem">添加</el-button>
      <el-button type="danger">删除</el-button>
    </el-row>
      <el-row
        ><el-table
          border
          id="middle"
          show-summary
          :data="newMonthTable"
          @select="newRowClick"
        >
          <el-table-column label="费用月度预算表">
            <el-table-column type="selection" width="50px"></el-table-column>
            <el-table-column
              type="index"
              label="序号"
              fixed
              width="55px"
            ></el-table-column
          >
                  <el-table-column
          v-for="item in monthBugetTableList"
          :label="item.label"
          :width="item.width"
          :key="item.index"
        >
        <template slot-scope="scope">
              <el-input :disabled="!isSameMonth" v-model="scope.row[item.prop]"></el-input>
            </template>
        </el-table-column></el-table-column>
          </el-table
      ></el-row>
          <el-row type="flex" justify="end">
      <el-button>取消</el-button>
      <el-button type="primary" @click="submit">提交</el-button>
    </el-row>
    </el-dialog>
  </div>
</template>

<script>
import {
  GetAllBudgetsPerMonths,
  DeleteBudgetsPerMonth,
  InsertBudgetsPerMonth
} from "network/monthBuget.js";

import {Item} from "service/Business/month.js"

export default {
  data() {
    return {
      itemlist: [
        { label: "部门", value: "" },
        { label: "审核人", value: "" },
        { label: "填补日期", value: "" },
      ],
      monthBugetTableList: [
        { label: "类目", prop: "title", width: "" },
        { label: "程序名称", prop: "customerName", width: "" },
        { label: "项目明细", prop: "projectDetail", width: "" },
        { label: "金额", prop: "amount", width: "" },
        { label: "预付款日期", prop: "prepaymentsTime", width: "" },
        { label: "经办人", prop: "managerName", width: "" },
        { label: "备注", prop: "comment", width: "" },
      ],
      newMonthTable: [],
      userQuery:{beginTime:'2020-12-02',endTime:'2021-1-30',pageSize:10,pageNumber:1},
      monthBugetTable: [],
  emptyItem:{ title: "",
    customerName: "",
    projectDetail: "",
  amount: null,
    prepaymentsTime: "",
    managerName: "",
    comment: ""},
      currentMonth: null,
      selectMonth: null,
      isSameMonth: false,
      selectData: null,
      isNewDialogDisplay: false,
      selectData:null
    };
  },
  created() {
    var date = new Date();
    this.currentMonth = date.getMonth();
    GetAllBudgetsPerMonths(this.userQuery).then(res =>{
      this.monthBugetTable = res.result.budgetPerMonths;
    })
  },
  methods: {
    /* 日期选择 */
    changeDate() {
      this.isSameMonth = this.selectMonth.getMonth() === this.currentMonth;
      if (this.isSameMonth) {
        return;
      } else {
      }
    },
    //
    rowClick(selection) {
      this.selectData = selection;
    },
    //添加按钮
    add() {
      this.isNewDialogDisplay = true;
    },
    //删除
    removeUser(){
      var data = this.selectData;
      if(data){
        let arr = new Array(data.length);
        data.forEach((item,index)=>{
          arr[index] = item.id;
        })
        DeleteBudgetsPerMonth(arr).then(res=>{
          if(res.success){
            this.$message('删除成功');
          }
        })
      }
      else{
        this.$message.error('选择失败');
      }
    },
    //弹框添加一项
    addItem(){
      var item = new Item;
      this.newMonthTable.push(item);
    },
    //提交数据
    submit(){
      var data = this.newMonthTable;
      this.newMonthTable.forEach(item=>{
        item.id = 0;
        item.creationTime ="2020-12-27"
      })
      InsertBudgetsPerMonth(data).then(res=>{
        if(res.success){
          this.newMonthTable = null;
          this.isNewDialogDisplay = false;
        }
      })
    }
  },
};
</script>

<style scoped>
.header .el-button {
  width: 100px;
}
.el-row {
  margin-bottom: 10px;
  line-height: 40px;
}
#summary .text-left .el-input {
  width: 65%;
}
#middle .el-table-column {
  text-align: center;
}
.table-header {
  border: 1px solid;
  border-style: solid none none solid;
}
</style>